
body {
  height: 120vh;
  color: #fff;
  background: #333;
}

/* Navbar */

 .navbar {
   background: #333 !important;
 }
.Hamburger {
  width: 30px;
  height: 30px;
  cursor: pointer;
}

.Hamburger>div {
  width: 30px;
  height: 2px;
  margin: 6px 0;
  background: #fff;
}

/* Carousel */
.carousel {
  margin: 56px 0 100px;
  height: 400px;
  background:#333;
  position: relative;
}
/* Carousel Control */
.carousel-control-prev-icon,
.carousel-control-next-icon {
  height: 60px;
  width: 60px;
  outline: black;
  border-radius: 50%;
  border: 1px solid black;
  background-image: none;
  transition: all .3s ease;
  position: relative;
}
.carousel-control-prev-icon:hover, 
.carousel-control-next-icon:hover {
  background: #f3f3f3;
} 

.carousel-control-next-icon:after
{
  content: '>';
  position: absolute;
  font-size: 50px;
  left: 15px;
  top: -12px;
  color: red;
}

.carousel-control-prev-icon:after {
  content: '<';
  font-size: 50px;
  right: 15px;
  top: -12px;
  color: red;
  position: absolute;
}

.carousel-item {
  background: #333;
  height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.carousel-item:nth-of-type(2) {
  /* background: url(./item2.jpg);
  background-size: cover;
  background-position: center; */
  height: 400px;
}
.carousel-item:nth-of-type(3) {
  /* background: rgb(46, 52, 133); */
  height: 400px;
}

.carousel-indicators {
  position: absolute;
  right: 0px;
  bottom: 0;
  transition: transform .3s ease;
}

.carousel-indicators .active {
  width: 30px;
  border-radius: 60% !important;
}
.carousel-indicators li {
  width: 10px;
  height: 10px;
  border-radius: 100%;
  margin: 0 5px;
}

/* Product Card */
.card {
  width: 240px;
  margin: 30px 1em;
  color: #333;
}

.card-body {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.card h2 {
  font-size: 1rem;
}

.price {
  font-size: 2rem;
}

/* Contact Info */
#contactInfo {
  margin-top: 100px;
  margin-bottom: 100px;
}

.map {
  height: 200px;
  background: rgb(90, 89, 89);
  margin-right: 1em;
}
.contact {
  height: 200px;
  background: rgb(184, 41, 41);
}

@media (max-width:599px) {
  .carousel-control-prev-icon,
.carousel-control-next-icon {
  display: none;
}

}